<template>
  <div :class="css.wrapper">
    <div :class="css.flex">
      <div :class="css.block">
        <div :class="css.description" :title="name">{{ name }}</div>
        <div :class="css.title">Имя карточки</div>
      </div>
      <div :class="css.block">
        <div :class="css.description">{{ status }}</div>
        <div :class="css.title">Статус</div>
      </div>
    </div>
    <div :class="css.flex">
      <div :class="css.block">
        <div :class="css.description">{{ balance }}</div>
        <div :class="css.title">Баланс</div>
      </div>
      <div :class="css.block">
        <div :class="css.description">{{ type | filterType }}</div>
        <div :class="css.title">Тип</div>
      </div>
    </div>
  </div>
</template>

<script>
import { WALLET_TYPE } from '@/config/constants';
import css from './WalletCard.css';
export default {
  name: 'WalletCard',
  props: {
    name: {
      type: String
    },
    status: {
      name: String
    },
    type: {
      name: String
    },
    balance: {
      type: String
    }
  },
  filters: {
    filterType(id) {
      if (typeof id === 'number') {
        switch (id) {
          case WALLET_TYPE.card:
            return 'Карточка';
          case WALLET_TYPE.bank:
            return 'Банк';
          case WALLET_TYPE.cash:
            return 'Наличка';
          default:
            return 'хз';
        }
      }
      return '';
    }
  },
  data() {
    return {
      css
    };
  }
};
</script>
