<template>
  <div :class="css.wrapper" v-click-outside="clickOutside">
    <div
      :class="css.placeholder"
      tabindex="1"
      @click="toggleDropdown"
      @keyup.enter="toggleDropdown"
    >
      {{ title }}
      <span :class="css.arrow">
        <arrow />
      </span>
    </div>
    <ul :class="css.dropdown" v-if="isOpen && dropdownList">
      <li
        v-for="item in dropdownList"
        tabindex="1"
        :key="item.id"
        :class="[css.item, value === item.id && css.activeItem]"
        @click="chooseItem(item.name, item.id)"
        @keyup.enter="chooseItem(item.name, item.id)"
      >
        {{ item.name }}
      </li>
    </ul>
  </div>
</template>

<script>
import arrow from './static/down-arrow.svg';
import css from './Dropdown.css';
export default {
  name: 'Dropdown',
  props: {
    placeholder: {
      type: String,
      default: 'Выбире чтото'
    },
    dropdownList: {
      type: Array,
      require: true
    },
    value: {
      type: Number
    }
  },
  data() {
    return {
      isOpen: false,
      title: this.placeholder,
      css
    };
  },
  methods: {
    toggleDropdown() {
      this.isOpen = !this.isOpen;
    },
    chooseItem(name, id) {
      this.title = name;
      this.$emit('input', id);
      this.isOpen = false;
    },
    chooseActiveItem() {
      this.dropdownList.forEach(item => {
        if (item.id === this.value) {
          this.title = item.name;
        }
      });
    },
    clickOutside() {
      this.isOpen = false;
    }
  },
  mounted() {
    if (this.value && !this.value.length) this.chooseActiveItem();
  },
  components: {
    arrow
  }
};
</script>
