<template>
  <div :class="css.wrapper">
    <button
      :key="tab.id"
      v-for="tab in tabs"
      @click="change($event, tab.id)"
      :class="[css.tab, activeTab === tab.id && css.activeTab]"
    >
      {{ tab.name }}
    </button>
    <div :class="css.active" :style="{ left: this.left }"></div>
  </div>
</template>

<script>
import css from './Tabs.css';
export default {
  name: 'Tabs',
  props: {
    tabs: {
      require: true,
      type: Array
    },
    activeTab: {
      require: true,
      type: Number
    }
  },
  data() {
    return {
      active: 0,
      left: '0px',
      css
    };
  },
  methods: {
    change(e, id) {
      this.left = `${e.target.offsetLeft}px`;
      this.$emit('change', id);
    }
  }
};
</script>
